import React, { useContext } from 'react';
import { Container, Grid, makeStyles, Theme, createStyles } from '@material-ui/core';
import { GlobalContext } from 'src/stores';
import Page from 'components/Layout/Page';
import LoginForm from 'components/Forms/Login';
import Logo from 'components/Utils/LubyconLogo';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      paddingTop: theme.spacing(20),
      paddingBottom: theme.spacing(10),
      [theme.breakpoints.down('md')]: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(10),
      },
    },
    logo: {
      marginBottom: '2.5rem',
    },
  })
);

export const Login = () => {
  const styles = useStyles();
  const { themeType } = useContext(GlobalContext);
  return (
    <Page pageName="로그인">
      <Container className={styles.root}>
        <Grid justify="center" container>
          <Grid xs={12} md={6} lg={4} item>
            <div className={styles.logo}>
              <Logo color={themeType} size={40} />
            </div>
            <LoginForm />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default Login;
